.header__background {
  grid-row: 1;
  -ms-grid-row: 1;
  grid-column: 1 / 3;
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  background: #11a4cb;
}

.header__logo {
  grid-row: 1;
  -ms-grid-row: 1;
  grid-column: 2;
  -ms-grid-column: 2;
  height: 63px;
  margin-top: 42px;
  font-family: RobotoSlab;
  font-size: 48px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #005b6b;
}

.header__description {
  -ms-grid-column: 3;
  padding: 41px 19px 37px 43px;
  font-size: 20px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #000000;
}

.header__title {
  -ms-grid-column: 4;
  margin: 44px 0 0 55px;
  height: 19px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #000000;
}

@media screen and (max-width: 900px) {
  .header__background {
    grid-row: 2 / 4;
    grid-column: 1 / 4;
  }

  .header__title {
    grid-row: 1;
    grid-column: 1 / 4;
    margin: 11px 14px 10px;
    text-align: right;
  }

  .header__logo {
    grid-row: 2;
    margin: 25px 0 0;
    height: 34px;
    font-size: 32px;
  }

  .header__description {
    grid-row: 3;
    grid-column: 2;
    padding: 19px 0 24px;
    font-size: 12px;
    font-weight: 500;
    color: #fff;
  }

  .header__description .bold {
    font-weight: 900;
  }
}
.field {
  position: relative;
}

.field__label {
  width: 56px;
  height: 16px;
  font-size: 13px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.23;
  letter-spacing: normal;
  white-space: nowrap;
  color: #666666;
}

.field__placeholder {
  pointer-events: none;
  position: absolute;
  left: 0;
  right: 0;
  z-index: -1;
  box-sizing: content-box;
  height: 24px;
  padding: 4px 0 6px;
  font-size: 20px;
  font-weight: 300;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #cccccc;
}

.field__input {
  box-sizing: content-box;
  height: 24px;
  padding: 4px 0 6px;
  border-bottom: .1px solid #ccc;
  font-size: 20px;
  font-weight: 300;
  color: #000000;
}

.field-error .field__input {
  border-color: #d0021b;
}

.field__error {
  position: absolute;
  left: 0;
  right: 0;
  height: 16px;
  font-size: 13px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.23;
  letter-spacing: normal;
  color: #d0021b;
}
.checkbox {
  cursor: pointer;
  width: 12px;
  height: 12px;
  border-radius: 2px;
  box-shadow: 0 0 0 1px #888 inset;
  text-align: center;
}

.checkbox.checked {
  background-color: #888;
}

.checkbox.checked:after {
  content: '\2713';
  position: relative;
  top: -2px;
  font-size: 9px;
  color: white;
}
.form {
  display: flex;
  flex-direction: column;
  -ms-grid-row: 2;
  grid-column: 2 / 4;
  -ms-grid-column: 2;
  -ms-grid-column-span: 2;
  padding: 46px 0 93px;
  font-size: 16px;
  font-weight: 300;
  color: #000;
}

.form__label {
  margin: 46px 0 25px;
  height: 17px;
  font-size: 14px;
  font-weight: 700;
  color: #000000;
}

.form__grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -12px -29px;
}

.col {
  margin: 0 12px 29px;
}

.col-md-8 {
  width: calc(100% / 1.5 - 24px);
}

.col-md-4 {
  width: calc(100% / 3 - 24px);
}

.col-md-2 {
  width: calc(100% / 6 - 24px);
}

.form__controls {
  margin-top: 60px;
}

.option {
  display: flex;
  align-items: baseline;
  font-size: 13px;
  font-weight: 300;
  line-height: 1.23;
  color: #000000;
}

.option__checkbox {
  flex-shrink: 0;
  margin-right: 12px;
}

.option__strong {
  display: block;
  font-size: 20px;
  line-height: normal;
}

.option__error {
  display: block;
  color: #d0021b;
}

.form__increment {
  padding-bottom: 15px;
}

.increment {
  padding: 29px 24px 31px;
  background-color: #eee;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.43;
  color: #4a4a4a;
  margin-bottom: 23px;
  -webkit-transition: padding .25s ease-in-out, margin .25s ease-in-out;
  transition: padding .25s ease-in-out, margin .25s ease-in-out;
}

.increment-hidden {
  margin: 0;
  padding: 0;
  font-size: 0;
  -webkit-transition-delay: .25s;
          transition-delay: .25s;
}

.increment__header {
  font-size: 18px;
  line-height: 1.11;
  color: #005b6b;
}

.increment__button {
  cursor: pointer;
  font-size: 18px;
  font-weight: 900;
  color: #005b6b;
  text-decoration: underline;
  text-transform: uppercase;
}

.increment__text {
  -webkit-transition: font-size .25s ease-in-out, opacity .25s ease-in-out .25s;
  transition: font-size .25s ease-in-out, opacity .25s ease-in-out .25s;
}

.increment-hidden .increment__text {
  opacity: 0;
  font-size: 0;
  -webkit-transition: font-size .25s ease-in-out .25s, opacity .25s ease-in-out;
  transition: font-size .25s ease-in-out .25s, opacity .25s ease-in-out;
}

.form__send {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-top: 9px;
}

.form__agreement {
  width: 390px;
}

.form__button {
  width: 177px;
  height: 48px;
  border-radius: 33px;
  background-color: #ff9810;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
}

@media screen and (max-width: 900px) {
  .form {
    grid-column: 2;
    padding: 30px 0 52px;
    font-size: 14px;
    font-weight: 300;
    color: #000;
  }

  .form__text {
    margin-bottom: -22px;
  }

  .form__grid {
    margin: 0 -12px -34px;
  }

  .col {
    margin: 0 12px 34px;
  }

  .col-12 {
    width: calc(100% - 24px);
  }

  .col-7 {
    width: calc(100% / 12 * 7 - 24px);
  }

  .col-6 {
    width: calc(50% - 24px);
  }

  .col-5 {
    width: calc(100% / 12 * 5 - 24px);
  }

  .form__controls {
    margin-top: 45px;
  }

  .increment {
    margin-left: -24px;
    margin-right: -24px;
  }

  .increment__header {
    font-size: 16px;
  }

  .form__send {
    display: block;
  }

  .form__agreement {
    width: auto;
  }

  .form__button {
    margin-top: 54px;
    width: 100%;
  }
}
.footer__background {
  grid-row: 3;
  -ms-grid-row: 3;
  grid-column: 1 / 5;
  -ms-grid-column: 1;
  -ms-grid-column-span: 4;
  background: #11a4cb;
}

.footer__content {
  grid-row: 3;
  -ms-grid-row: 3;
  grid-column: 2 / 4;
  -ms-grid-column: 2;
  -ms-grid-column-span: 2;
  padding: 41px 0 49px;
  font-size: 14px;
  font-weight: 500;
  color: #ffffff;
}

.footer__copyright {
  margin-right: 29px;
}

.footer__link {
  cursor: pointer;
  text-decoration: underline;
}

@media screen and (max-width: 900px) {
  .footer__background {
    grid-row: 5;
    grid-column: 1 / 4;
  }

  .footer__content {
    grid-row: 5;
    grid-column: 2;
    display: flex;
    flex-direction: column;
    padding: 29px 0 21px;
    text-align: center;
  }

  .footer__copyright {
    order: 1;
    margin: 18px 0 0;
  }
}
* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  font: inherit;
}

p {
  margin: 0;
}

a {
  outline: none;
  color: inherit;
  text-decoration: inherit;
  font: inherit;
}

label {
  display: inline-block;
}

input {
  padding: 0;
  width: 100%;
  border: none;
  background: none;
  outline: none;
  font: inherit;
  color: inherit;
}

input[type="number"], input[type="datetime-local"], input[type="month"] {
  -moz-appearance: textfield;
}

input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button,
input[type="datetime-local"]::-webkit-outer-spin-button, input[type="datetime-local"]::-webkit-inner-spin-button,
input[type="month"]::-webkit-outer-spin-button, input[type="month"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input::-ms-clear {
  display: none;
}

input:disabled {
  cursor: not-allowed;
  color: inherit;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  border: none;
  background: none;
  outline: none;
  font: inherit;
}

select::-ms-expand {
  display: none;
}

button {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 0;
  background: none;
  border: none;
  outline: none;
  font: inherit;
  color: inherit;
}

button:disabled {
  cursor: not-allowed;
  color: inherit;
}

option {
  padding: 0;
  font: inherit;
  color: inherit;
}

[contenteditable] {
  cursor: text;
  outline: none;
}

table {
  border-collapse: collapse;
  border-spacing: unset;
  text-align: left;
}

th {
  padding: 0;
  font: inherit;
}

td {
  padding: 0;
}

hr {
  border: none;
  margin: 0;
}
